import React, { useEffect, useState} from "react";
import { useDispatch, useSelector } from "react-redux";
import { connectM } from "./redux/blockchain/blockchainActions";
import { connect, isConnected } from "./redux/stakeBlockchain/blockchainActions";
import { fetchData, balance, rewards} from "./redux/stakeData/dataActions";
import {fetchDataM, hasApproved } from "./redux/data/dataActions2";

import "./styles/stake.css";

const Staking = () => {
    const dispatch = useDispatch();
    const blockchain = useSelector((state) => state.blockchain);
    const data = useSelector((state) => state.data);
    const [feedback, setFeedback] = useState("STAKE");
    const [claiming, setClaiming] = useState(false)
    const [staking, setStaking] = useState(false);
    const [unstaking, setUnstaking] = useState(false);
    const [hasStaked, setHasStaked] = useState(false);
    const [_stake, _unStake] = useState("STAKE");
    const account = blockchain.account;
    const [tokenId, setTokenId] = useState([]);
    const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });
    useEffect(()=>{
        dispatch(connectM())
        
    },[])

    useEffect(()=>{
        setTimeout(() => {
            hasApproved ? dispatch(connect()) : dispatch(connectM())
        }, 3000);
    },[])
    const getConfig = async () => {
    const configResponse = await fetch("/stakeConfig/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
        SET_CONFIG(config);
    };
    const getDataM = () => {
        if (account !== "" && blockchain.smartContract !== null) {
            dispatch(fetchDataM())
        }
    };
    const getData = () => {
        if (account !== "" && blockchain.smartContract !== null) {
            dispatch(fetchData())
        }
    };
    useEffect(() => {
        getConfig();
    }, []);
    useEffect(() => {
        getDataM();
        getData();
    }, [account]);

    const stake = (tokenId) => {
        setStaking(true);
        setFeedback("BUSY")
        dispatch(connect())
        blockchain.smartContract.methods.deposit([tokenId])
            .send({
                to: CONFIG.CONTRACT_ADDRESS,
                from: account
            })
            .once("error", (err) => {
                console.log(err)
                setStaking(false)
            })
            .then((Response)=>{
                setStaking(false)
            })
        
        setFeedback("STAKE")   
    }


    const unStake = async (tokenId) => {
        setUnstaking(true)
        dispatch(connect())
        blockchain.smartContract.methods.unstakeByIds([tokenId])
            .send({
                to: CONFIG.CONTRACT_ADDRESS,
                from: account
            })
            .once("error", (err) => {
                console.log(err)
                setUnstaking(false)
            })
            .then((Response)=>{
                setUnstaking(false)
            })
    }

    const unstakeAll = () => {
        setUnstaking(true)
        dispatch(connect())
        blockchain.smartContract.methods.unstakeAll()
            .send({
                to: CONFIG.CONTRACT_ADDRESS,
                from: account
            })
            .once("error", (err) => {
                console.log(err)
                setUnstaking(false)
            })
            .then((Response)=>{
                setUnstaking(false)
            })                
    }
    
    const approve = async () => {
        dispatch(connectM());
        await blockchain.smartContract.methods
        .setApprovalForAll(CONFIG.CONTRACT_ADDRESS, true)
        .send({
            to: CONFIG.CONTRACT_ADDRESS,
            from: account
        })
        dispatch(connect())
    }

    const claim = async (tokenId) => {
        setClaiming(true)
        dispatch(connect())
        await blockchain.smartContract.methods.withdraw()
        .send({
            to: CONFIG.CONTRACT_ADDRESS,
            from: account
        })
            .once("error", (err) => {
                console.log(err)
                setClaiming(false)
            })
            .then((Response)=>{
                setClaiming(false)
            })
    }
    const handleChange = (e) =>{
        e.preventDefault();
        setTokenId(parseInt(e.target.value.split(" ")));
    }

    return ( 
        <main className="stake-app">
            <div>
                <button 
                    className="connect-btn"
                    onClick={(e)=>{
                        !isConnected ?
                        dispatch(connectM())
                        : e.preventDefault()
                    } }
                >
                    {!isConnected ? "Connect Wallet" : account.slice(0, 10)}
                </button>
            </div>
            <h1>STAKE</h1>            
            <h2>
                {staking ? "staking.." : ""}
                {unstaking ? "unstaking.." : ""}
                {claiming ? "claiming.." : ""}
            </h2>

            <div className="card-container">
                <div>
                    <div className="titles">
                        <h2>Stake LPs</h2>
                        <p>Earn rewards by staking LPs</p>
                    </div>
                    <blockquote className="totalcounts">
                    {' '}
                    {/* {balance / 10 ** 18} */}
                    {' '}0 LP Tokens
                    </blockquote>
                    <form className="form" onChange={handleChange}>
                        <button 
                            className="btn stake-btn" 
                            onClick={hasApproved ? (e) => {
                                e.preventDefault()
                                stake(tokenId)
                                } : 
                                (e) => {
                                e.preventDefault()
                                approve(tokenId)
                                }}
                                >
                            {hasApproved ? "STAKE" : "APPROVE"}
                        </button>
                    </form>                    
                </div>                
            </div>
            <div className="card-container">
                <div>
                    <div className="titles">
                        <h2>Unstake LPs</h2>
                        <p>Withdraw your LP tokens from staking</p>
                    </div>
                    <blockquote className="totalcounts">
                    {' '}
                    {/* {balance / 10 ** 18} */}
                    {' '}0 LP Staked
                    </blockquote>
                    <button 
                            className="btn mb-5"
                            onClick={unstakeAll}>
                            Unstake LP
                        </button>                   
                </div>                
            </div>
            <div className="card-container">
                <div>
                    <div className="titles">
                        <h2>Claim Liquidity Rewards</h2>
                        <p>Claim Rewards</p>
                    </div>
                    <blockquote className="totalcounts">
                    {' '}
                    {/* {balance / 10 ** 18} */}
                    {' '}0 $AMMO
                    </blockquote>
                    <button 
                        className="btn mb-5"
                        onClick={claim}>
                        Claim LP Rewards
                    </button>                  
                </div>                
            </div>
            <div className="card-container">
                <div>
                    <div className="titles">
                        <h2>Stake Your NFTs</h2>
                        <p>Earn rewards by staking</p>
                    </div>
                    <blockquote className="totalcounts">
                    {' '}
                    {/* {balance / 10 ** 18} */}
                    {' '}0 NFTs
                    </blockquote>
                    <form className="form" onChange={handleChange}>
                        <button 
                            className="btn stake-btn" 
                            onClick={hasApproved ? (e) => {
                                e.preventDefault()
                                stake(tokenId)
                                } : 
                                (e) => {
                                e.preventDefault()
                                approve(tokenId)
                                }}
                                >
                            {hasApproved ? "STAKE" : "APPROVE"}
                        </button>
                    </form>                    
                </div>                
            </div>
            <div className="card-container">
                <div>
                    <div className="titles">
                        <h2>Unstake</h2>
                        <p>Unstake your staked NFTs</p>
                    </div>
                    <blockquote className="totalcounts">
                    {' '}
                    {/* {balance / 10 ** 18} */}
                    {' '}0 NFTs staked
                    </blockquote>
                    <button 
                            className="btn mb-5"
                            onClick={unstakeAll}>
                            UNSTAKE ALL
                        </button>                   
                </div>                
            </div>
            <div className="card-container">
                <div>
                    <div className="titles">
                        <h2>Claim Rewards</h2>
                        <p>0 NFT(s) staked</p>
                    </div>
                    <blockquote className="totalcounts">
                    {' '}
                    {/* {balance / 10 ** 18} */}
                    {' '}0 $AMMO
                    </blockquote>
                    <button 
                        className="btn mb-5"
                        onClick={claim}>
                        CLAIM
                    </button>                  
                </div>                
            </div>

            <div className="footersection">
                {/* <img src="logo.png" alt="logo" className="footerlogo" /> */}
                <h2>Join our community</h2>
                <div class="sociallogins">
                    <figure>
                    <a href="https://opensea.io/collection/dogfacearmynft">
                        <img src="data:image/png;base64," alt="Twitter" />
                    </a>
                    </figure>
                    <figure>
                    <a href="https://www.twitter.com/dogfaceNFT" target="_blank">
                        <img src="data:image/png;base64," alt="Twitter" />
                    </a>
                    </figure>
                    <figure>
                    <a href="https://discord.gg/a5M6j3fnC7" target="_blank">
                        <img src="data:image/png;base64," alt="Discord" />
                    </a>
                    </figure>
                    <figure>
                    <a href="https://www.youtube.com/c/fleececrypto" target="_blank">
                        <img src="data:image/png;base64," alt="Youtube" />
                    </a>
                    </figure>
                    <figure>
                        <a href="https://www.reddit.com/r/DogfaceDAOnft/">
                        <img src="data:image/png;base64," alt="Reddit" />
                    </a>
                    </figure>
                </div>
                <p style={{ color: "#ffffff" }}>©2022 DogFace Army. All rights reserved.</p>
            </div>
            
        </main>
    );
}
export default Staking ;
